import uniCountdown from '@/components/uni-countdown/uni-countdown.vue';
import moveVerify from "@/components/helang-moveVerify/helang-moveVerify.vue"
export default {
	data() {
		return {
			username:"",
			phone:"",
			resultData:{},
			isSure:true,
			isVerify:false,
			isSend:true,
			sendText:"发送验证码"
		}
	},
	components: {  "move-verify":moveVerify ,uniCountdown },
	methods:{
		/* 校验结果回调函数 */
		verifyResult(res){
			if(res.flag){
				this.isVerify = true ;
			}
			this.resultData = res;
		},
		/* 校验插件重置 */
		verifyReset(){
			this.$refs.verifyElement.reset();

			/* 删除当前页面的数据 */
			this.resultData = {};
		},
		isOver(){
			this.sendText="再次发送"
			this.isSend = true;
		},
		sendVerify(){
			console.log(111)
			this.isSend = false;
		}
	}
}
